@font-face {
  font-family: "Inter";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Inter-Regular"), local("Inter Regular"), url("../fonts/Inter-Regular.woff") format("woff"), url("../fonts/Inter-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Inter-Medium"), local("Inter Medium"), url("../fonts/Inter-Medium.woff") format("woff"), url("../fonts/Inter-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Inter-Semibold"), local("Inter Semibold"), url("../fonts/Inter-Semibold.woff") format("woff"), url("../fonts/Inter-Semibold.woff2") format("woff2");
}
*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.4;
  color: #162b3f;
  background-color: #ffffff;
}
body.no-scroll {
  overflow: hidden;
}
@media screen and (min-width: 46.25rem) {
  body {
    font-size: 1.25rem;
    line-height: 1.4;
  }
}

.body-wrapper {
  overflow: hidden;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}

.body-wrapper.dark::after {
  content: "";
  position: fixed;
  top: -100vh;
  left: -100vw;
  width: 300vw;
  height: 300vh;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(12px);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;
}
@media screen and (min-width: 46.25rem) {
  .container {
    padding: 0 4.5rem;
  }
}
@media screen and (min-width: 90rem) {
  .container {
    padding: 0 7.5rem;
    max-width: 90rem;
  }
}

.heading--size-l {
  font-weight: 500;
  font-size: 2.5rem;
  line-height: 1.2;
  letter-spacing: -0.06em;
}
@media screen and (min-width: 46.25rem) {
  .heading--size-l {
    font-size: 5rem;
  }
}
.heading--size-m {
  font-weight: 500;
  font-size: 1.75rem;
  line-height: 1.2;
}
@media screen and (min-width: 46.25rem) {
  .heading--size-m {
    font-size: 3rem;
  }
}
.heading--size-s {
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.4;
}

.input {
  position: relative;
  border-radius: 0.75rem;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.135) 100%);
  backdrop-filter: blur(2px);
}
.input input, .input textarea {
  display: block;
  border: 0;
  background: none;
  width: 100%;
  height: 3.1rem;
  padding: 0 1.5rem;
  font-size: 0.88rem;
  line-height: 1.15;
  color: #ffffff;
  resize: vertical;
  transition: 0.3s outline-color ease-in;
  outline-offset: -2px;
  border-radius: 0.75rem;
}
.input input:focus, .input textarea:focus {
  outline: 2px solid #ffffff;
}
.input input::placeholder, .input textarea::placeholder {
  color: rgba(255, 255, 255, 0.6);
}
@media screen and (min-width: 46.25rem) {
  .input input, .input textarea {
    height: 4rem;
  }
}
.input textarea {
  padding: 1.5rem;
  min-height: 6.25rem;
}
.input::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 0.75rem;
  padding: 2px;
  opacity: 0.5;
  background: linear-gradient(104.59deg, rgba(255, 255, 255, 0.8) 0.76%, rgba(255, 255, 255, 0.204911) 32.78%, rgba(255, 255, 255, 0.58807) 69.11%, rgba(255, 255, 255, 0.176) 99%);
  mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  mask-composite: exclude;
  pointer-events: none;
  transition: 0.3s opacity ease-in;
}
.input:hover::before {
  opacity: 0.7;
}
.input:active {
  opacity: 1;
}

.checkbox input {
  appearance: none;
  position: absolute;
}

.checkbox__box {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 5px;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.135) 100%);
  backdrop-filter: blur(2px);
}
.checkbox__box::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 5px;
  padding: 2px;
  background: linear-gradient(104.59deg, #FFFFFF 0.76%, rgba(255, 255, 255, 0.256139) 32.78%, rgba(255, 255, 255, 0.735087) 69.11%, rgba(255, 255, 255, 0.22) 99%);
  mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  mask-composite: exclude;
  pointer-events: none;
  opacity: 0.6;
  transition: 0.3s opacity ease-in;
}

.checkbox input:checked ~ .checkbox__box::after {
  content: "";
  position: absolute;
  width: 0.88rem;
  height: 0.6rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='11' viewBox='0 0 16 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3CforeignObject x='-4' y='-4' width='23.5' height='18.0859'%3E%3Cdiv xmlns='http://www.w3.org/1999/xhtml' style='backdrop-filter:blur(2px);height:100%25;width:100%25'%3E%3C/div%3E%3C/foreignObject%3E%3Cpath data-figma-bg-blur-radius='4' d='M1 5L4.33347 8.75015C4.70123 9.16389 5.33514 9.20014 5.74767 8.83103L14.5 1' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A");
  background-size: 100% auto;
  background-position: center center;
}

.checkbox input:checked ~ .checkbox__box::before {
  opacity: 1;
}

.btn-submit {
  padding: 0.88rem 1.5rem;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.3;
  border: 0;
  background: #ffffff;
  border-radius: 10rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}
.btn-submit span {
  background-image: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
  background-clip: text;
  color: transparent;
}
.btn-submit::after {
  content: "";
  width: 1.25rem;
  height: 1.25rem;
  mask-image: url("../img/icons/arrow-right.svg");
  mask-repeat: no-repeat;
  mask-size: 100% 100%;
  background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
}
@media screen and (min-width: 46.25rem) {
  .btn-submit {
    padding: 1.5rem;
  }
}

.card-decor {
  position: absolute;
  aspect-ratio: 166/96;
}
.card-decor--orange {
  background: url(../img/card-orange.svg) no-repeat center/100% auto, linear-gradient(90deg, #f26f21 0, #ef522d 64.01%, #ee4036 100.02%);
}
.card-decor--white {
  background: url(../img/card-white.svg) no-repeat center/100% auto, #ffffff;
}

.select {
  height: 100%;
}
.select--crypto .select__btn {
  width: 8.25rem;
}
.select--crypto .select__btn::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 1px;
  height: 70%;
  background: #ffffff;
}

.select__btn {
  position: relative;
  padding: 0;
  height: 100%;
  background: none;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 0.88rem;
  line-height: 1.15;
  color: #ffffff;
}
.select__btn::after {
  content: "";
  width: 1rem;
  height: 1rem;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5L8 11L15 5' stroke='white' stroke-linecap='round'/%3E%3C/svg%3E%0A");
}

.select__menu {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  order: -1;
  width: 7.25rem;
  border-radius: 0.75rem;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.135) 100%);
}
.select__menu--crypto {
  left: 0;
  right: 0;
  top: 100%;
  translate: 0 0.75rem;
  width: auto;
  padding: 0.75rem 1.5rem;
}
.select__menu[hidden] {
  display: none;
}
.select__menu > li[aria-selected=true] {
  order: -1;
  border-radius: 0.75rem 0.75rem 0 0;
  padding: 1.5rem 1.2rem 1.5rem 1.5rem;
  background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.select__menu > li[aria-selected=true]::after {
  content: "";
  width: 1rem;
  height: 1rem;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5L8 11L15 5' stroke='white' stroke-linecap='round'/%3E%3C/svg%3E%0A");
  rotate: 180deg;
}
.select__menu li {
  display: flex;
  justify-content: start;
  padding: 0.75rem 1.5rem;
  overflow: hidden;
}
.select__menu li:not(.select__menu li[aria-selected=true]):hover {
  background: rgba(255, 255, 255, 0.3);
}
.select__menu::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 0.75rem;
  padding: 2px;
  opacity: 1;
  mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  mask-composite: exclude;
  pointer-events: none;
  transition: 0.3s opacity ease-in;
  background: linear-gradient(104.59deg, rgba(255, 255, 255, 0.3) 0.76%, rgba(255, 255, 255, 0.0768416) 32.78%, rgba(255, 255, 255, 0.220526) 69.11%, rgba(255, 255, 255, 0.066) 99%);
}

.select__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fill, 5.6rem);
}
.select__list li {
  position: relative;
  padding: 0.75rem 0.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  border-radius: 0.75rem;
  font-size: 0.88rem;
  line-height: 1.15;
}
.select__list li[aria-selected=true] {
  background: rgba(255, 255, 255, 0.3);
}
.select__list li[aria-selected=true]::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 0.75rem;
  padding: 2px;
  mask: linear-gradient(#000 0 0) content-box, linear-gradient(#000 0 0);
  mask-composite: exclude;
  pointer-events: none;
  transition: 0.3s opacity ease-in;
  background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
}
.select__list li:not(.select__list li[aria-selected=true]):hover {
  background: rgba(255, 255, 255, 0.3);
}

.select__menu-tabs {
  margin: 0 0 1.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.75rem;
}

.select__menu-tab {
  padding: 0.75rem;
  border-radius: 0.75rem;
  font-size: 0.88rem;
  line-height: 1.15;
}
.select__menu-tab--active {
  background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
}

.header--opened-menu {
  top: 0;
  width: 100%;
  position: fixed;
  z-index: 100;
}

.header__container {
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (min-width: 46.25rem) {
  .header__container {
    padding: 3.1rem 0 5rem;
    justify-content: start;
    gap: 2.25rem;
  }
}
@media screen and (min-width: 90rem) {
  .header__container {
    padding: 3.1rem 0;
    justify-content: space-between;
  }
}

.header__button {
  display: none;
  padding: 0.75rem 1.25rem;
  font-size: 1rem;
  line-height: 1.3;
  font-weight: 400;
  text-decoration: none;
  color: #ffffff;
  background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
  border-radius: 10rem;
  gap: 0.5rem;
  align-items: center;
}
.header__button::after {
  content: "";
  width: 1.25rem;
  height: 1.25rem;
  background: url("../img/icons/arrow-right.svg") no-repeat 0 0/100% 100%;
}
@media screen and (min-width: 46.25rem) {
  .header__button {
    display: inline-flex;
  }
}

@media screen and (min-width: 46.25rem) {
  .header__nav {
    margin-left: auto;
  }
}
@media screen and (min-width: 90rem) {
  .header__nav {
    margin: 0;
  }
}

.main-nav--opened .main-nav__wrapper {
  display: flex;
}

.main-nav__wrapper {
  display: none;
  align-items: center;
  justify-content: center;
  z-index: -1;
  position: fixed;
  inset: 0;
  background: white;
}
.main-nav__wrapper::before {
  content: "";
  position: absolute;
  top: 40%;
  left: 0;
  translate: -60% -30%;
  width: 157vw;
  height: 184vw;
  background: #EE4235;
  opacity: 0.3;
  filter: blur(181.45px);
  transform: rotate(-155.19deg);
  z-index: -1;
}
.main-nav__wrapper::after {
  content: "";
  position: absolute;
  top: 30%;
  right: 0;
  width: 135vw;
  height: 363vw;
  background: #F26B23;
  opacity: 0.3;
  filter: blur(181.45px);
  transform: rotate(-152.32deg);
  z-index: -1;
}
@media screen and (min-width: 90rem) {
  .main-nav__wrapper {
    position: static;
    display: block;
    background: none;
  }
  .main-nav__wrapper::before, .main-nav__wrapper::after {
    display: none;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.1rem;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media screen and (min-width: 90rem) {
  .main-nav__list {
    gap: 3.5rem;
    flex-direction: row;
  }
}

.main-nav__link {
  font-size: 1.75rem;
  font-weight: 500;
  text-decoration: none;
  color: #000000;
}
.main-nav__link--active {
  color: #ffffff;
  padding: 1.4rem 2.5rem;
  border-radius: 10rem;
  background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
}
@media screen and (min-width: 90rem) {
  .main-nav__link--active {
    padding: 0.88rem 1.75rem;
  }
}
@media screen and (min-width: 90rem) {
  .main-nav__link {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.3;
  }
}

.main-nav__button {
  position: relative;
  width: 1.13rem;
  height: 0.75rem;
  background: none;
  border: 0;
}
.main-nav__button span {
  position: absolute;
  display: block;
  width: 100%;
  left: 0;
  height: 2px;
  background: #000000;
}
.main-nav__button span:nth-child(1) {
  top: 0;
}
.main-nav__button span:nth-child(2) {
  top: 50%;
  translate: 0 -50%;
}
.main-nav__button span:nth-child(3) {
  bottom: 0;
}
.main-nav__button::after {
  content: "";
  width: 2rem;
  height: 2rem;
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}
@media screen and (min-width: 90rem) {
  .main-nav__button {
    display: none;
  }
}

.promo {
  position: relative;
  padding: 0 0 97vw;
}
.promo--main {
  background: url("../img/promo-bg-mobile.png") no-repeat bottom -3rem center/150% auto;
  padding: 0 0 97vw;
}
.promo--main::before {
  content: "";
  position: absolute;
  width: 158vw;
  height: 184vw;
  background: #EE4235;
  opacity: 0.3;
  left: -11rem;
  top: 6rem;
  filter: blur(181.45px);
  border-radius: 50%;
  z-index: -1;
  rotate: -155deg;
}
@media screen and (min-width: 46.25rem) {
  .promo--main::before {
    width: 85vw;
    height: 99.7vw;
    opacity: 0.2;
    left: 0;
    top: 50%;
    translate: -50% -50%;
  }
}
@media screen and (min-width: 90rem) {
  .promo--main::before {
    width: 39.4rem;
    height: 46rem;
    top: 50%;
    translate: -50% 0;
    opacity: 0.3;
  }
}
.promo--main::after {
  content: "";
  position: absolute;
  top: 4rem;
  left: 26%;
  width: 135vw;
  height: 363vw;
  rotate: -152deg;
  border-radius: 50%;
  background: #f26b23;
  opacity: 0.3;
  filter: blur(181.45px);
  z-index: -1;
}
@media screen and (min-width: 46.25rem) {
  .promo--main::after {
    width: 73.3vw;
    height: 196.6vw;
    top: 20%;
    right: 0;
    translate: 50% 0;
    rotate: -159deg;
  }
}
@media screen and (min-width: 90rem) {
  .promo--main::after {
    width: 34rem;
    height: 91rem;
    top: -14rem;
    right: -7rem;
    translate: 0 0;
  }
}
@media screen and (min-width: 46.25rem) {
  .promo--main {
    padding: 0 0 51vw;
    background: url("../img/promo-bg-tablet.png") no-repeat bottom -10vw right -30vw/95% auto;
  }
}
@media screen and (min-width: 90rem) {
  .promo--main {
    padding: 4.38rem 0 8.75rem;
    background: url(../img/promo-bg-desktop.png) no-repeat bottom -5rem right -8rem;
  }
}
.promo--about {
  padding: 0 0 15vw;
  background: url("../img/promo-about-bg-mobile.png") no-repeat bottom -125% center/auto 70%;
}
@media screen and (min-width: 46.25rem) {
  .promo--about {
    background: url(../img/promo-about-bg-tablet.png) no-repeat bottom -32vw center/126% auto;
  }
}
@media screen and (min-width: 90rem) {
  .promo--about {
    background: none;
  }
}
.promo--about::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  translate: -30% 70%;
  width: 157vw;
  height: 184vw;
  background: #EE4235;
  opacity: 0.25;
  z-index: -1;
  filter: blur(181.45px);
  rotate: -155.19deg;
}
@media screen and (min-width: 46.25rem) {
  .promo--about::before {
    width: 85vw;
    height: 99vw;
    translate: -50% 60%;
  }
}
@media screen and (min-width: 46.25rem) {
  .promo--about::after {
    content: "";
    position: absolute;
    top: 0;
    right: -20%;
    width: 543.47px;
    height: 1455.11px;
    background: #f26b23;
    opacity: 0.3;
    filter: blur(181.45px);
    transform: rotate(-152.32deg);
  }
}
.promo--crypto {
  padding: 0;
  margin: 0 0 2.25rem;
}
@media screen and (min-width: 90rem) {
  .promo--crypto {
    padding: 4.3rem 0 7.5rem;
  }
  .promo--crypto .promo__text {
    width: 46.2rem;
  }
}
.promo--contact {
  padding: 0 0 4.5rem;
  background: url("../img/promo-contact-mobile.png") no-repeat bottom -17rem center;
}
.promo--contact::before {
  content: "";
  position: absolute;
  width: 130vw;
  height: 363vw;
  background: #F26B23;
  opacity: 0.3;
  filter: blur(181.45px);
  transform: rotate(-152.32deg);
  z-index: -1;
}
@media screen and (min-width: 90rem) {
  .promo--contact {
    background: url("../img/contact-promo-bg.png") no-repeat bottom -40rem right;
  }
}

.promo__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 46.25rem) {
  .promo--main .promo__container, .promo--contact .promo__container {
    align-items: start;
  }
}

@media screen and (min-width: 90rem) {
  .promo--contact .promo__container {
    display: grid;
    grid-template-columns: 36.75rem 1fr;
    gap: 0 2.5rem;
  }
}

@media screen and (min-width: 90rem) {
  .promo--contact .promo__heading {
    align-self: end;
    grid-row: 1/2;
    grid-column: 1/-1;
  }
}

@media screen and (min-width: 90rem) {
