.start-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  border-radius: 1.5rem;
  background: $gradient-primary;
  color: $color-default-white;

  @media screen and (min-width: $md-viewport) {
    padding: 3rem 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    padding: 3rem 4.8rem;
    flex-direction: row;
  }
}

.start-form__select-input {
  position: relative;
  height: 3.38rem;
  display: flex;
  z-index: 1;

  div:nth-child(1) {
    flex: 0
  }

  div:nth-child(2) {
    flex: 1;
  }
}

.start-form__block {
  width: 100%;
  display: grid;
  gap: 1.5rem;
}

.start-form__footer {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;

  @media screen and (min-width: $md-viewport) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.start-form__amount {
  display: flex;
  align-items: center;
  padding: 0 1.5rem;

  input {
    display: block;
    border: 0;
    background: none;
    width: 100%;
    height: 3.1rem;
    padding: 0 1.5rem;
    font-size: 0.88rem;
    line-height: 1.15;
    color: $color-default-white;
    resize: vertical;

    transition: 0.3s outline-color ease-in;
    outline-offset: -2px;
    border-radius: 0.75rem;

    &::placeholder {
      color: rgba($color-default-white, 60%);
    }
  }
}

.start-form__name {
  margin: 0;
}

.start-form__note {
  margin: 0;
  font-size: 0.75rem;
  line-height: 1.3;
  text-transform: uppercase;

  a {
    color: inherit;
  }
}

.start-form__reverse {
  padding: 0.75rem;
  display: block;
  background: none;
  border: 0;
}

.start-form__block-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.start-form__rate {
  margin: 0;
  font-size: 0.88rem;
  line-height: 1.15;

  &::before {
    content: "Rate: ";
  }
}

.start-form__input-agree {
  display: flex;
  align-items: center;
  gap: 1.25rem;

  @media screen and (min-width: $md-viewport) {
    width: 15rem;
  }
}

.start-form__amount-crypto {
  margin-left: 0.2rem;
  font-size: 0.88rem;
  line-height: 1.15;
}

.start-form__button {
  padding: 1.25rem 2.5rem;
}
