.mission {
  position: relative;
  margin: 0 0 2.25rem;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 5.25rem;

    &::before {
      content: "";
      width: 86vw;
      height: 135vw;
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
      translate: -40% -80%;
      background: #ee4235;
      opacity: 0.4;
      filter: blur(181.45px);
      transform: rotate(-95.86deg);
    }
  }
}

.mission__container {
  @media screen and (min-width: $lg-viewport) {
    display: grid;
    grid-template-columns: 1fr 36.75rem;
    gap: 0 7.8rem;
  }
}

.mission__heading {
  margin: 0 0 0.75rem;

  @media screen and (min-width: $lg-viewport) {
    grid-column: 2 / 3;
  }
}

.mission__text {
  margin: 0 0 2.25rem;
  display: grid;
  gap: 1rem;
  color: rgba($color-default-black, 60%);
  font-size: 1rem;
  line-height: 1.4;

  p {
    margin: 0;
  }

  @media screen and (min-width: $md-viewport) {
    width: 85%;
    margin: 0 0 3rem;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 90%;
    grid-column: 2 / 3;
    margin: 0;
    font-size: 1.15rem;
  }
}

.mission__bg {
  width: 100%;
  aspect-ratio: 354 / 168;
  border-radius: 1.5rem;
  background: url("../img/mission-bg-mobile.png") no-repeat 0 0 / 100% 100%;

  @media screen and (min-width: $md-viewport) {
    background-image: url("../img/mission-bg-tablet.jpg");
  }

  @media screen and (min-width: $lg-viewport) {
    height: 100%;
    aspect-ratio: auto;
    grid-row: 1 / 3;
  }
}
