.select {
  height: 100%;

  &--crypto .select__btn {
    width: 8.25rem;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 1px;
      height: 70%;
      background: $color-default-white;
    }
  }
}

.select__btn {
  position: relative;
  padding: 0;
  height: 100%;
  background: none;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  font-family: $ff-inter;
  font-size: 0.88rem;
  line-height: 1.15;
  color: $color-default-white;

  &::after {
    content: "";
    width: 1rem;
    height: 1rem;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5L8 11L15 5' stroke='white' stroke-linecap='round'/%3E%3C/svg%3E%0A");
  }
}

.select__menu {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  order: -1;
  width: 7.25rem;
  border-radius: 0.75rem;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.135) 100%);

  &--crypto {
    left: 0;
    right: 0;
    top: 100%;
    translate: 0 0.75rem;
    width: auto;
    padding: 0.75rem 1.5rem;
  }

  &[hidden] {
    display: none;
  }

  & > li[aria-selected="true"] {
    order: -1;
    border-radius: 0.75rem 0.75rem 0 0;
    padding: 1.5rem 1.2rem 1.5rem 1.5rem;
    background: $gradient-primary;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &::after {
      content: "";
      width: 1rem;
      height: 1rem;
      background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5L8 11L15 5' stroke='white' stroke-linecap='round'/%3E%3C/svg%3E%0A");
      rotate: 180deg;
    }
  }

  li {
    display: flex;
    justify-content: start;
    padding: 0.75rem 1.5rem;
    overflow: hidden;

    &:not(&[aria-selected="true"]):hover {
      background: rgba($color-default-white, 30%);
    }
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 0.75rem;
    padding: 2px;
    opacity: 1;
    mask: linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
    mask-composite: exclude;
    pointer-events: none;
    transition: 0.3s opacity ease-in;
    background: linear-gradient(104.59deg, rgba(255, 255, 255, 0.3) 0.76%, rgba(255, 255, 255, 0.0768416) 32.78%, rgba(255, 255, 255, 0.220526) 69.11%, rgba(255, 255, 255, 0.066) 99%);
  }
}

.select__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fill, 5.6rem);

  li {
    position: relative;
    padding: 0.75rem 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.4rem;
    border-radius: 0.75rem;
    font-size: 0.88rem;
    line-height: 1.15;

    &[aria-selected="true"] {
      background: rgba($color-default-white, 30%);

      &::before {
        content: "";
        position: absolute;
        inset: 0;
        border-radius: 0.75rem;
        padding: 2px;
        mask: linear-gradient(#000 0 0) content-box,
        linear-gradient(#000 0 0);
        mask-composite: exclude;
        pointer-events: none;
        transition: 0.3s opacity ease-in;
        background: $gradient-primary;
      }
    }

    &:not(&[aria-selected="true"]):hover {
      background: rgba($color-default-white, 30%);
    }
  }
}


.select__menu-tabs {
  margin: 0 0 1.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.75rem;
}

.select__menu-tab {
  padding: 0.75rem;
  border-radius: 0.75rem;
  font-size: 0.88rem;
  line-height: 1.15;

  &--active {
    background: $gradient-primary;
  }
}
