.feedback-section {
  position: relative;
  margin: 0 0 2.25rem;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -57%;
    width: 247vw;
    height: 223vw;
    background: #F26B23;
    opacity: 0.35;
    rotate: -152deg;
    filter: blur(181.45px);
    border-radius: 50%;
    translate: -60% 0;
    z-index: -1;

    @media screen and (min-width: $md-viewport) {
      display: none;
    }
  }

  &::after {
    content: "";
    position: absolute;
    right: -100%;
    top: 0;
    width: 86vw;
    height: 193vw;
    translate: -50% -50%;
    background: #ee4235;
    opacity: 0.4;
    filter: blur(181.45px);
    transform: rotate(158.35deg);
    z-index: -1;

    @media screen and (min-width: $lg-viewport) {
      display: block;
      width: 40rem;
      height: 65.6rem;
      top: -10rem;
      right: 0;
      translate: 80% -20%;
    }
  }

  &--black {
    margin-top: -1.5rem;
    padding: 5.25rem 0;
    background: $color-default-black;
    color: $color-default-white;
    border-radius: 1.5rem;

    .feedback-section__text {
      color: rgba(#e5e5e5, 60%);
    }

    @media screen and (min-width: $md-viewport) and (max-width: $lg-viewport) {
      margin-top: -2rem;

      .feedback-section__container {
        margin: 0 4.5rem;
      }

      .feedback-section__wrapper {
        grid-template-columns: 1fr;
        gap: 2.25rem;

        * {
          grid-column: 1 / -1;
        }
      }

      .feedback-section__text {
        width: 68%;
      }
    }
  }

  @media screen and (min-width: $md-viewport) {
    margin-bottom: 5.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 6.5rem
  }
}

.feedback-section__container {
  @media screen and (min-width: $lg-viewport) {
    display: grid;
    grid-template-columns: 30rem 1fr;
    grid-template-rows: min-content 1fr;
    gap: 0 7.5rem;
    align-content: start;
  }
}

.feedback-section__heading {
  margin: 0 0 0.75rem;


  span {
    background-image: $gradient-primary;
    background-clip: text;
    color: transparent;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-column: 1 / 2;
  }
}

.feedback-section__text {
  margin: 0 0 2.25rem;
  color: $color-text-secondary;

  @media screen and (min-width: $md-viewport) {
    width: 60%;
    margin: 0 0 5.25rem;
    font-size: 1.13rem;
    line-height: 1.3;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-column: 1 / 2;
    width: 95%;
    margin: 0;
  }
}

.feedback-section__wrapper {
  padding: 1.5rem;
  border-radius: 1.5rem;
  background: $gradient-primary;
  display: grid;
  gap: 1.5rem;

  @media screen and (min-width: $md-viewport) {
    grid-template-columns: 1fr 1fr;
    padding: 3rem 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
  }
}

.feedback-section__input:last-of-type {
  @media screen and (min-width: $md-viewport) {
    grid-column: 1 / -1;

    textarea {
      height: 12rem;
    }
  }
}

.feedback-section__button {
  @media screen and (min-width: $md-viewport) {
    grid-column: 1 / -1;
  }
}
