.card-decor {
  position: absolute;
  aspect-ratio: 166 / 96;

  &--orange {
    background:
            url(../img/card-orange.svg) no-repeat center / 100% auto,
            linear-gradient(90deg, #f26f21 0, #ef522d 64.01%, #ee4036 100.02%);
  }

  &--white {
    background:
            url(../img/card-white.svg) no-repeat center / 100% auto,
            $color-default-white;
  }
}
