.aml {
  position: relative;
  margin: 0 0 3rem;

  &::before {
    content: "";
    width: 130%;
    height: 40%;
    position: absolute;
    top: 10%;
    left: -30%;
    translate: -80% 0;

    background: #EE4235;
    opacity: 0.4;
    filter: blur(181.45px);
    transform: rotate(-167.71deg);
    z-index: -1;
  }

  &::after {
    content: "";
    width: 140%;
    height: 48%;
    position: absolute;
    bottom: 10%;
    right: -30%;
    translate: 60% 0;
    opacity: 0.3;
    background: #F26B23;
    filter: blur(181.45px);
    transform: rotate(167.71deg);
    z-index: -1;
  }
}

.aml__nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 1.25rem;
  flex-wrap: wrap;
  align-items: center;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 2.5rem;
  }
}

.nav-text__link {
  font-size: 0.75rem;
  line-height: 1.3;
  text-decoration: none;
  text-transform: uppercase;
  color: rgba($color-default-black, 60%);

  &--active {
    position: relative;
    display: inline-block;
    color: transparent;
    background-image: $gradient-primary;
    background-clip: text;
    padding: 0.6rem 1.25rem;
    border-radius: 100rem;

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      border-radius: 10rem;
      padding: 1px;
      background: $gradient-primary;
      mask: linear-gradient(#000 0 0) content-box,
      linear-gradient(#000 0 0);
      mask-composite: exclude;
      pointer-events: none;
    }
  }
}

.aml__text {
  font-size: 0.88rem;
  line-height: 1.4;
  color: rgba($color-default-black, 60%);

  h1 {
    margin: 0 0 0.75rem;
    color: $color-default-black;
  }

  ol {
    counter-reset: list;
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    gap: 1rem;

    & > li {
      counter-increment: list;
      &::before {
        content: counter(list) ". ";
      }

    }
  }

  ul {
    margin: 1.5rem 0;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 80%;
  }
}
