.header {
  &--opened-menu {
    top: 0;
    width: 100%;
    position: fixed;
    z-index: 100;
  }
}

.header__container {
  padding: 1.5rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: $md-viewport) {
    padding: 3.1rem 0 5rem;
    justify-content: start;
    gap: 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    padding: 3.1rem 0;
    justify-content: space-between;
  }
}

.header__button {
  display: none;
  padding: 0.75rem 1.25rem;
  font-size: 1rem;
  line-height: 1.3;
  font-weight: 400;
  text-decoration: none;
  color: $color-default-white;
  background: $gradient-primary;
  border-radius: 10rem;
  gap: 0.5rem;
  align-items: center;

  &::after {
    content: "";
    width: 1.25rem;
    height: 1.25rem;
    background: url("../img/icons/arrow-right.svg") no-repeat 0 0 / 100% 100%;
  }


  @media screen and (min-width: $md-viewport) {
    display: inline-flex;
  }
}

.header__nav {
  @media screen and (min-width: $md-viewport) {
    margin-left: auto;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0;
  }
}
