.risk {
  overflow: hidden;
  position: relative;
  margin: 0 0 3rem;
  padding: 5.25rem 0;
  background: radial-gradient(50% 50% at 50% 50%, #210202 0%, #000000 100%);
  border-radius: 1.5rem;
  color: $color-default-white;

  @media screen and (min-width: $lg-viewport) {
    overflow: hidden;
    padding: 7.5rem 0;
    margin: 0 0 7.5rem;
  }
}

.about-us .risk {
  margin: -3vw 0 5.25rem;

  @media screen and (min-width: $lg-viewport) {
    padding: 0;
    background: none;
  }
}

.about-us .risk__container {
  @media screen and (min-width: $lg-viewport) {
    overflow: hidden;
    padding: 7.5rem;
    background: radial-gradient(50% 50% at 50% 50%, #210202 0%, #000000 100%);
    border-radius: 1.5rem;
  }
}

.crypto .risk {
  @media screen and (min-width: $lg-viewport) {
    z-index: 2;
  }
}

.risk__container {
  @media screen and (min-width: $lg-viewport) {
    position: relative;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: -71vw;
    left: 0;
    width: 160vw;
    height: 252vw;
    opacity: 0.6;
    background: #ee4235;
    filter: blur(181.45px);
    transform: rotate(-95.86deg);

    @media screen and (min-width: $md-viewport) {
      width: 58vw;
      height: 79vw;
      left: auto;
      right: 0;
      bottom: 10vw;
      translate: 80% 0;
      opacity: 1;
      transform: rotate(-143.2deg);
    }

    @media screen and (min-width: $lg-viewport) {
      width: 18.8rem;
      height: 34rem;
      right: 8rem;
      bottom: 1rem;
      translate: 0;
    }
  }

  &::after {
    @media screen and (min-width: $md-viewport) {
      content: "";
      position: absolute;
      top: 20%;
      translate: -90% 0;
      width: 75.8vw;
      height: 48.3vw;
      background: #f26b23;
      filter: blur(181.45px);
      transform: rotate(-105.86deg);
    }

    @media screen and (min-width: $lg-viewport) {
      width: 35rem;
      height: 22.3rem;
      left: -2rem;
      top: 9.3rem;
      translate: -50% 0;
    }
  }
}

.about-us .risk__container {

  &::before {
    background: #F26B23;
    translate: 0 30%;
    opacity: 0.3;

    @media screen and (min-width: $lg-viewport) {
      background: #EE4235;
      opacity: 0.5;
    }
  }
}

.risk__heading {
  margin: 0 0 0.75rem;
  text-align: center;
}

.crypto .risk__heading {
  margin: 0 0 2.25rem;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 3rem;
  }
}

.risk__description {
  margin: 0 0 2.25rem;
  color: #e5e5e5;
  text-align: center;

  @media screen and (min-width: $md-viewport) {
    width: 37.5rem;
    margin: 0 auto 3rem;
    font-size: 1.13rem;
    line-height: 1.3;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 auto 6.25rem;
  }
}

.risk__list {
  position: relative;
  z-index: 1;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1.5rem;

  @media screen and (min-width: $md-viewport) {
    grid-template-columns: 1fr 1fr;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.about-us .risk__list {
  grid-template-columns: 1fr 1fr;
}

.risk__item {
  position: relative;
  padding: 2.5rem 1.5rem;
  border-radius: 0.75rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba($color-default-white, 4%);

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 0.75rem;
    padding: 1px;
    background: linear-gradient(272.36deg, rgba(77, 77, 77, 0.6) -14.37%, rgba(179, 179, 179, 0.6) 116.83%);
    mask: linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
    mask-composite: exclude;
  }

  @media screen and (min-width: $md-viewport) {
    font-size: 1rem;
    line-height: 1.3;
  }
}

.risk__item-icon {
  margin: 0 0 1.5rem;
}

.risk__item-name {
  margin: 0 0 0.75rem;
  text-align: center;
}

.risk__item-text {
  margin: 0;
  text-align: center;
  color: $color-gray;
}
