.our-partners {
  margin: 0 0 3rem;

  @media screen and (min-width: $md-viewport) {
    position: relative;
    z-index: 1;
    margin: -10vw 0 5rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 8.3rem;
  }
}

.our-partners__container {
  padding: 3rem 1rem;
  background: $color-default-white;
  border-radius: 1.5rem;
}

.our-partners__heading {
  margin: 0 0 2.3rem;
  text-align: center;
}

.our-partners__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: $lg-viewport) {
    gap: 4.5rem;
  }
}
