.input {
  position: relative;
  border-radius: 0.75rem;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.135) 100%);
  backdrop-filter: blur(2px);

  input, textarea {
    display: block;
    border: 0;
    background: none;
    width: 100%;
    height: 3.1rem;
    padding: 0 1.5rem;
    font-size: 0.88rem;
    line-height: 1.15;
    color: $color-default-white;
    resize: vertical;

    transition: 0.3s outline-color ease-in;
    outline-offset: -2px;
    border-radius: 0.75rem;

    &:focus {
      outline: 2px solid $color-default-white;
    }

    &::placeholder {
      color: rgba($color-default-white, 60%);
    }

    @media screen and (min-width: $md-viewport) {
      height: 4rem;
    }
  }

  textarea {
    padding: 1.5rem;
    min-height: 6.25rem;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 0.75rem;
    padding: 2px;
    opacity: 0.5;
    background: linear-gradient(104.59deg, rgba(255, 255, 255, 0.8) 0.76%, rgba(255, 255, 255, 0.204911) 32.78%, rgba(255, 255, 255, 0.58807) 69.11%, rgba(255, 255, 255, 0.176) 99%);
    mask: linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
    mask-composite: exclude;
    pointer-events: none;
    transition: 0.3s opacity ease-in;
  }

  &:hover::before {
    opacity: 0.7;
  }

  &:active {
    opacity: 1;
  }
}
