.advantages {
  margin: 0 0 3rem;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 5.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 7.5rem
  }
}

.advantages__container {
  @media screen and (min-width: $lg-viewport) {
    display: grid;
    gap: 6.8rem;
    grid-template-columns: 31rem 1fr;
    align-items: end;
  }
}

.advantages__list {
  margin: -1.5rem 0 4.75rem;
  padding: 0;
  list-style: none;

  @media screen and (min-width: $md-viewport) {
    margin: -2.25rem 0 4rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: -1.5rem 0;
  }
}

.advantages__item {
  padding: 1.5rem 0;
  display: grid;
  grid-template-columns: 1.5rem 1fr;
  gap: 1.5rem;

  &:not(:last-child) {
    border-bottom: 2px solid #e5e5e5;
  }

  @media screen and (min-width: $md-viewport) {
    padding: 2.25rem 0;
    font-size: 1.25rem;
    line-height: 1.4;
    grid-template-columns: 3rem 1fr;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-template-columns: auto;
  }
}

.advantages__item-icon {
  width: 100%;
  height: auto;

  @media screen and (min-width: $lg-viewport) {
    width: 3rem;
  }
}

.advantages__item-text {
  margin: 0;
}

.advantages__decoration {
  position: relative;
  width: 100%;
  aspect-ratio: 354 / 386;
  background: $gradient-primary;
  border-radius: 1.5rem;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    aspect-ratio: 354 / 484;
    background: url("../img/advantages-bg-mobile.png") no-repeat 0 0 / 100% 100%;

    @media screen and (min-width: $md-viewport) {
      background: url("../img/advantages-bg-tablet.png") no-repeat right bottom / 93% auto;
    }

    @media screen and (min-width: $lg-viewport) {
      background-size: 100% 100%;
    }
  }
}

.advantages__decoration-card {
  &--white {
    top: 5.8rem;
    right: -0.5rem;
    width: 37vw;
    border-radius: 1.25rem;
    rotate: -15.73deg;

    @media screen and (min-width: $md-viewport) {
      width: 32.2vw;
      top: 20vw;
      right: 4.6vw;
    }

    @media screen and (min-width: $lg-viewport) {
      width: 15rem;
      top: 11rem;
      right: 3rem;
    }
  }

  &--orange {
    width: 41.75vw;
    border-radius: 0.75rem;
    rotate: 15.8deg;
    bottom: 2.5rem;

    @media screen and (min-width: $md-viewport) {
      width: 39.1vw;
      bottom: 9vw;
      left: -4vw;
    }

    @media screen and (min-width: $lg-viewport) {
      width: 17rem;
      bottom: 4rem;
      left: -4rem;
    }
  }
}
