.start {
  position: relative;
  background: url("../img/bg-start-mobile.png") no-repeat top center / 158% auto;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    translate: -50% 0;
    width: 157vw;
    height: 184.5vw;
    background: #ee4235;
    border-radius: 50%;
    opacity: 0.4;
    filter: blur(181.45px);
    transform: rotate(-155.19deg);
    z-index: -1;

    @media screen and (min-width: $md-viewport) {
      display: none;
    }
  }

  &::before {
    content: "";
    position: absolute;
    width: 135vw;
    height: 363vw;
    background: #f26b23;
    opacity: 0.3;
    filter: blur(181.45px);
    transform: rotate(-152.32deg);
    border-radius: 50%;
    z-index: -1;

    @media screen and (min-width: $md-viewport) {
      top: -20%;
      scale: 0.5;
    }
  }

  @media screen and (min-width: $md-viewport) {
    margin: calc(-8.5rem - 3vw) 0 0;
    padding: 17.5vw 0 0;
    background: url("../img/bg-start-tablet.png") no-repeat top -17vw center/190% auto;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0;
    padding: 0;
    background: none;
  }
}

.crypto {
  @media screen and (min-width: $lg-viewport) {
    background: url("../img/start-bg-desktop.png") no-repeat top -4rem center/142rem auto;
  }
}

.start__heading {
  margin: 0 0 0.75rem;
  text-align: center;
}

.start__description {
  margin: 0 2.5rem 2.25rem;
  text-align: center;

  @media screen and (min-width: $lg-viewport) {
    font-size: 1.1rem;
    line-height: 1.3;
    color: rgba($color-default-black, 60%);
  }
}
