*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;

  font-family: $ff-inter;
  font-weight: 400;
  line-height: 1.4;
  color: $color-accent-secondary;
  background-color: $color-default-white;

  &.no-scroll {
    overflow: hidden;
  }

  @media screen and (min-width: $md-viewport) {
    font-size: 1.25rem;
    line-height: 1.4;
  }
}

.body-wrapper {
  overflow: hidden;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}

.body-wrapper.dark {
  &::after {
    content: "";
    position: fixed;
    top: -100vh;
    left: -100vw;
    width: 300vw;
    height: 300vh;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(12px);
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1.5rem;

  @media screen and (min-width: $md-viewport) {
    padding: 0 4.5rem;
  }

  @media screen and (min-width: $lg-viewport) {
    padding: 0 7.5rem;
    max-width: $lg-viewport;
  }
}
