.heading {
  &--size-l {
    font-weight: 500;
    font-size: 2.5rem;
    line-height: 1.2;
    letter-spacing: -0.06em;

    @media screen and (min-width: $md-viewport) {
      font-size: 5rem;
    }
  }

  &--size-m {
    font-weight: 500;
    font-size: 1.75rem;
    line-height: 1.2;

    @media screen and (min-width: $md-viewport) {
      font-size: 3rem;
    }
  }

  &--size-s {
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 1.4;
  }
}
