.checkbox {

}

.checkbox input {
  appearance: none;
  position: absolute;
}

.checkbox__box {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 5px;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.135) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  backdrop-filter: blur(2px);

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 5px;
    padding: 2px;
    background: linear-gradient(104.59deg, #FFFFFF 0.76%, rgba(255, 255, 255, 0.256139) 32.78%, rgba(255, 255, 255, 0.735087) 69.11%, rgba(255, 255, 255, 0.22) 99%);
    mask: linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
    mask-composite: exclude;
    pointer-events: none;
    opacity: 0.6;
    transition: 0.3s opacity ease-in;
  }

}

.checkbox input:checked ~ .checkbox__box::after {
  content: "";
  position: absolute;
  width: 0.88rem;
  height: 0.6rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='11' viewBox='0 0 16 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3CforeignObject x='-4' y='-4' width='23.5' height='18.0859'%3E%3Cdiv xmlns='http://www.w3.org/1999/xhtml' style='backdrop-filter:blur(2px);height:100%25;width:100%25'%3E%3C/div%3E%3C/foreignObject%3E%3Cpath data-figma-bg-blur-radius='4' d='M1 5L4.33347 8.75015C4.70123 9.16389 5.33514 9.20014 5.74767 8.83103L14.5 1' stroke='white' stroke-width='2' stroke-linecap='round'/%3E%3C/svg%3E%0A");
  background-size: 100% auto;
  background-position: center center;
}

.checkbox input:checked ~ .checkbox__box::before {
  opacity: 1;
}
