.main-nav {
  &--opened .main-nav__wrapper {
    display: flex;
  }
}

.main-nav__wrapper {
  display: none;
  align-items: center;
  justify-content: center;
  z-index: -1;
  position: fixed;
  inset: 0;
  background: white;

  &::before {
    content: "";
    position: absolute;
    top: 40%;
    left: 0;
    translate: -60% -30%;
    width: 157vw;
    height: 184vw;
    background: #EE4235;
    opacity: 0.3;
    filter: blur(181.45px);
    transform: rotate(-155.19deg);
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 30%;
    right: 0;
    width: 135vw;
    height: 363vw;
    background: #F26B23;
    opacity: 0.3;
    filter: blur(181.45px);
    transform: rotate(-152.32deg);
    z-index: -1;
  }

  @media screen and (min-width: $lg-viewport) {
    position: static;
    display: block;
    background: none;

    &::before, &::after {
      display: none;
    }
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.1rem;
  margin: 0;
  padding: 0;
  list-style: none;

  @media screen and (min-width: $lg-viewport) {
    gap: 3.5rem;
    flex-direction: row;
  }
}

.main-nav__link {
  font-size: 1.75rem;
  font-weight: 500;
  text-decoration: none;
  color: $color-default-black;

  &--active {
    color: $color-default-white;
    padding: 1.4rem 2.5rem;
    border-radius: 10rem;
    background: $gradient-primary;

    @media screen and (min-width: $lg-viewport) {
      padding: 0.88rem 1.75rem;
    }
  }

  @media screen and (min-width: $lg-viewport) {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.3;
  }
}

.main-nav__button {
  position: relative;
  width: 1.13rem;
  height: 0.75rem;
  background: none;
  border: 0;

  span {
    position: absolute;
    display: block;
    width: 100%;
    left: 0;
    height: 2px;
    background: $color-default-black;

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(2) {
      top: 50%;
      translate: 0 -50%;
    }

    &:nth-child(3) {
      bottom: 0;
    }
  }

  &::after {
    content: "";
    width: 2rem;
    height: 2rem;
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%
  }

  @media screen and (min-width: $lg-viewport) {
    display: none;
  }
}
