.who-is {
  margin: 0 0 2.25rem;
  position: relative;
  z-index: 1;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 5.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 9.3rem;
  }
}

.who-is__container {
  @media screen and (min-width: $lg-viewport) {
    display: grid;
    grid-template-columns: 1fr 36.75rem;
    gap: 0 7.8rem;
  }
}

.who-is__heading {
  margin: 0 0 0.75rem;

  @media screen and (min-width: $lg-viewport) {
    grid-column: 2 / 3;
  }
}

.who-is__description {
  margin: 0 0 2.25rem;
  color: $color-text-secondary;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 3rem;
    width: 84%;
    font-size: 1.13rem;
    line-height: 1.3;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-column: 2 / 3;
  }
}

.who-is__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1.5rem;

  @media screen and (min-width: $lg-viewport) {
    grid-column: 2 / 3;
  }
}

.who-is__item {
  display: grid;
  grid-template-columns: 1.88rem 1fr;
  gap: 0.75rem 1.5rem;

  @media screen and (min-width: $lg-viewport) {
    display: block;
  }
}

.who-is__item-icon {
  grid-row: 1 / 3;
  width: 1.88rem;
  height: 1.88rem;

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 1.5rem;
  }
}

.who-is__item-name {
  margin: 0;

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 0.75rem;
  }
}

.who-is__item-text {
  margin: 0 0 2.25rem;
  grid-column: 2 / 3;
  color: $color-gray;

  @media screen and (min-width: $md-viewport) {
    font-size: 1rem;
    line-height: 1.3;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 80%;
  }
}

.who-is__decoration {
  position: relative;
  width: 100%;
  aspect-ratio: 354 / 521;
  background: radial-gradient(50.01% 49.59% at 50.01% 50.01%, #230C0C 54.4%, #000000 100%);
  border-radius: 1.5rem;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100vw;
    aspect-ratio: 398 / 594;
    background: url("../img/who-is-bg-mobile.png") no-repeat 0 0 / 100% 100%;

    @media screen and (min-width: $md-viewport) {
      width: 112%;
      aspect-ratio: 538 / 801;
      background: url("../img/who-is-bg-tablet.png") no-repeat 0 0 / 100% 100%;
    }
  }

  &::after {
    content: "";
    position: absolute;
    width: 86.6vw;
    height: 136vw;
    z-index: -1;
    left: 50%;
    top: 50%;
    translate: -50% -50%;
    scale: 0.8;
    background: #ee4235;
    opacity: 0.4;
    filter: blur(181.45px);
    transform: rotate(-95.86deg);

    @media screen and (min-width: $lg-viewport) {
      width: 40rem;
      height: 63rem;
      top: -22rem;
      left: -29rem;
      translate: 20% 0;
      transform: rotate(-155.19deg);
    }
  }

  @media screen and (min-width: $md-viewport) {
    margin: 0 3.5rem;
    width: auto;
  }

  @media screen and (min-width: $lg-viewport) {
    grid-column: 1 / 2;
    grid-row: 1 / 4;
    margin: 0;
  }
}

.who-is__decoration-card {
  position: absolute;

  &--white {
    top: 2rem;
    left: 0.5rem;
    width: 36vw;
    rotate: 29deg;
    border-radius: 1.25rem;

    @media screen and (min-width: $lg-viewport) {
      width: 15rem;
      top: 1rem;
      left: 0;
    }
  }

  &--orange {
    width: 40.5vw;
    right: 0.5rem;
    bottom: 6.25rem;
    rotate: -16deg;
    border-radius: 0.75rem;

    @media screen and (min-width: $md-viewport) {
      right: -1rem;
    }

    @media screen and (min-width: $lg-viewport) {
      width: 17rem;
      right: -2rem;
      bottom: 10.25rem;
    }
  }
}
