.payment-systems {
  margin: 0 0 3rem;

  @media screen and (min-width: $md-viewport) {
    position: relative;
    margin: 0 0 5.25rem;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 40%;
      translate: -50% -70%;
      width: 133.5vw;
      height: 120vw;
      background: #f26b23;
      z-index: -1;
      opacity: 0.35;
      filter: blur(181.45px);
      transform: rotate(-152.32deg);
    }
  }

  @media screen and (min-width: $lg-viewport) {
    translate: 0 -100%;
    margin: -6.5rem 0 0;
    z-index: 1;

    &::before {
      display: none;
    }
  }
}

.payment-systems__container {
  padding: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  flex-wrap: wrap;
  background: $color-default-white;
  border-radius: 1.5rem;

  @media screen and (min-width: $md-viewport) {
    padding: 2.25rem;

    img {
      width: auto;
      height: 3.1rem;
    }
  }

  @media screen and (min-width: $lg-viewport) {
    width: 30rem;
  }
}
