.crypto-logos {
  width: 100vw;
  padding: 4.1rem 0;
  overflow: hidden;

  @media screen and (min-width: $md-viewport) {
    padding: 7.6rem 0
  }
}

.crypto-logos__container {
  display: inline-flex;
  align-items: center;
  gap: 2.25rem;
  width: max-content;
  animation: marquee 10s linear infinite;
  will-change: transform;

  @media screen and (min-width: $md-viewport) {
    gap: 4rem;
  }
}

.crypto-logos__container > img {
  flex: 0 0 auto;
  display: block;

  @media screen and (min-width: $md-viewport) {
    height: 4rem;
    width: auto;
  }
}

@keyframes marquee {
  from { transform: translate3d(0,0,0); }
  to   { transform: translate3d(-52.2%,0,0); }
}

@media (prefers-reduced-motion: reduce) {
  .crypto-logos__container { animation: none; }
}
