.footer {
  position: relative;
  margin: 0 0 3rem;
}

.index .footer {
  @media screen and (min-width: $lg-viewport) {
    &::before {
      content: "";
      position: absolute;
      width: 50.8rem;
      height: 42rem;
      left: 0;
      bottom: 0;
      translate: -20% 50%;
      background: #f26b23;
      opacity: 0.35;
      filter: blur(181.45px);
      transform: rotate(-152.32deg);
      z-index: -1;
    }
  }
}

.about-us .footer {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    translate: -20% -76%;
    width: 300vw;
    height: 155vw;
    background: #f26b23;
    opacity: 0.2;
    border-radius: 50%;
    filter: blur(181.45px);
    transform: rotate(-152.32deg);
    z-index: -1;

    @media screen and (min-width: $lg-viewport) {
      display: none;
    }
  }
}

.crypto .footer {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    translate: -20% -76%;
    width: 61rem;
    height: 61rem;
    background: #F26B23;
    opacity: 0.35;
    filter: blur(181.45px);
    transform: rotate(-152.32deg);
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 159vw;
    height: 358vw;
    background: #ee4235;
    opacity: 0.4;
    filter: blur(181.45px);
    transform: rotate(158.35deg);
    z-index: -1;
    translate: 50% 70%;

    @media screen and (min-width: $lg-viewport) {
      width: 40rem;
      height: 40rem;
      bottom: 0;
      right: 0;
      translate: 50% 50%;
      opacity: 0.4;
      rotate: 0;
    }
  }
}

.contact .footer {
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    translate: -20% 60%;
    width: 300vw;
    height: 155vw;
    background: #F26B23;
    opacity: 0.35;
    filter: blur(181.45px);
    transform: rotate(-152.32deg);
    z-index: -1;
  }
}

.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  border-radius: 1.5rem;
  background: $color-default-black;

  @media screen and (min-width: $md-viewport) {
    padding: 3rem 4.88rem;
  }
}

.footer__logo {
  display: block;
  margin: 0 0 1.5rem;
}

.footer__nav {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
  display: flex;
  gap: 0.75rem 2.5rem;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  &::after {
    content: "";
    margin: 1.5rem -1.5rem;
    width: calc(100% + 3rem);
    height: 1px;
    background: $color-gray;
    opacity: 0.2;
  }
}

.footer__nav-link {
  text-decoration: none;
  font-size: 0.88rem;
  line-height: 1.3;
  color: $color-default-white;

  @media screen and (min-width: $md-viewport) {
    font-size: 1rem;
  }
}

.footer__docs {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.footer__docs-link {
  color: $color-gray;
  font-size: 0.75rem;
  line-height: 1.4;
}

.footer__copyright {
  font-size: 0.88rem;
  line-height: 1.4;
  color: $color-gray;
  text-align: center;

  a {
    color: $color-gray;
  }
}
