.promo {
  position: relative;
  padding: 0 0 97vw;

  &--main {
    background: url("../img/promo-bg-mobile.png") no-repeat bottom -3rem center / 150% auto;
    padding: 0 0 97vw;

    &::before {
      content: "";
      position: absolute;
      width: 158vw;
      height: 184vw;
      background: #EE4235;
      opacity: 0.3;
      left: -11rem;
      top: 6rem;
      filter: blur(181.45px);
      border-radius: 50%;
      z-index: -1;
      rotate: -155deg;

      @media screen and (min-width: $md-viewport) {
        width: 85vw;
        height: 99.7vw;
        opacity: 0.2;
        left: 0;
        top: 50%;
        translate: -50% -50%;
      }

      @media screen and (min-width: $lg-viewport) {
        width: 39.4rem;
        height: 46rem;
        top: 50%;
        translate: -50% 0;
        opacity: 0.3;
      }
    }

    &::after {
      content: "";
      position: absolute;
      top: 4rem;
      left: 26%;
      width: 135vw;
      height: 363vw;
      rotate: -152deg;
      border-radius: 50%;
      background: #f26b23;
      opacity: 0.3;
      filter: blur(181.45px);
      z-index: -1;

      @media screen and (min-width: $md-viewport) {
        width: 73.3vw;
        height: 196.6vw;
        top: 20%;
        right: 0;
        translate: 50% 0;
        rotate: -159deg;
      }

      @media screen and (min-width: $lg-viewport) {
        width: 34rem;
        height: 91rem;
        top: -14rem;
        right: -7rem;
        translate: 0 0;
      }
    }

    @media screen and (min-width: $md-viewport) {
      padding: 0 0 51vw;
      background: url("../img/promo-bg-tablet.png") no-repeat bottom -10vw right -30vw / 95% auto;
    }

    @media screen and (min-width: $lg-viewport) {
      padding: 4.38rem 0 8.75rem;
      background: url(../img/promo-bg-desktop.png) no-repeat bottom -5rem right -8rem;
    }
  }

  &--about {
    padding: 0 0 15vw;
    background: url("../img/promo-about-bg-mobile.png") no-repeat bottom -125% center / auto 70%;

    @media screen and (min-width: $md-viewport) {
      background: url(../img/promo-about-bg-tablet.png) no-repeat bottom -32vw center / 126% auto;
    }

    @media screen and (min-width: $lg-viewport) {
      background: none;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      translate: -30% 70%;
      width: 157vw;
      height: 184vw;
      background: #EE4235;
      opacity: 0.25;
      z-index: -1;
      filter: blur(181.45px);
      rotate: -155.19deg;

      @media screen and (min-width: $md-viewport) {
        width: 85vw;
        height: 99vw;
        translate: -50% 60%;
      }
    }

    &::after {
      @media screen and (min-width: $md-viewport) {
        content: "";
        position: absolute;
        top: 0;
        right: -20%;
        width: 543.47px;
        height: 1455.11px;
        background: #f26b23;
        opacity: 0.3;
        filter: blur(181.45px);
        transform: rotate(-152.32deg);
      }
    }
  }

  &--crypto {
    padding: 0;
    margin: 0 0 2.25rem;

    @media screen and (min-width: $lg-viewport) {
      padding: 4.3rem 0 7.5rem;

      .promo__text {
        width: 46.2rem;
      }
    }
  }

  &--contact {
    padding: 0 0 4.5rem;
    background: url("../img/promo-contact-mobile.png") no-repeat bottom -17rem center;

    &::before {
      content: "";
      position: absolute;
      width: 130vw;
      height: 363vw;
      background: #F26B23;
      opacity: 0.3;
      filter: blur(181.45px);
      transform: rotate(-152.32deg);
      z-index: -1;
    }

    @media screen and (min-width: $lg-viewport) {
      background: url("../img/contact-promo-bg.png") no-repeat bottom -40rem right;
    }
  }
}

.promo__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.promo--main .promo__container, .promo--contact .promo__container {
  @media screen and (min-width: $md-viewport) {
    align-items: start;
  }
}

.promo--contact .promo__container {
  @media screen and (min-width: $lg-viewport) {
    display: grid;
    grid-template-columns: 36.75rem 1fr;
    gap: 0 2.5rem;
  }
}

.promo--contact .promo__heading {
  @media screen and (min-width: $lg-viewport) {
    align-self: end;
    grid-row: 1 / 2;
    grid-column: 1 / -1;
  }
}

.promo--contact .promo__text {
  @media screen and (min-width: $lg-viewport) {
    grid-row: 2 / 3;
    grid-column: 1 / -1;
  }
}

.promo__map {
  @media screen and (min-width: $lg-viewport) {
    grid-row: 1 / 3;
    grid-column: 2 / 3;
  }
}


.promo__chip {
  margin: 0 0 0.75rem;
  position: relative;
  padding: 0.6rem 1.25rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  border-radius: 100rem;
  font-size: 0.75rem;
  line-height: 1.3;
  text-transform: uppercase;
  background: $gradient-primary;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 100rem;
    padding: 2px;
    background: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);
    mask: linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
    mask-composite: exclude;
    pointer-events: none;
    transition: 0.3s opacity ease-in;
  }
}

.promo__heading {
  margin: 0 0 0.75rem;
  text-align: center;
}

.promo--main .promo__heading, .promo--main .promo__text {
  @media screen and (min-width: $md-viewport) {
    text-align: start;
  }
}

.promo--contact .promo__heading, .promo--contact .promo__text {
  @media screen and (min-width: $md-viewport) {
    text-align: start;
  }
}

.promo__text {
  margin: 0 0 1.5rem;
  color: $color-text-secondary;
  text-align: center;

  a {
    text-decoration: none;
    color: inherit;
  }

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 2.5rem;
    width: 80%;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 29.5rem
  }
}

.promo--about .promo__text {
  @media screen and (min-width: $lg-viewport) {
    width: 42.6rem;
  }
}

.promo__button {
  margin: 0 auto;
  padding: 0.85rem 2.5rem;
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  color: $color-default-white;
  background: $gradient-primary;
  text-decoration: none;
  border-radius: 10rem;
  font-size: 1rem;
  line-height: 1.3;

  &::after {
    content: "";
    width: 1.25rem;
    height: 1.25rem;
    background: url("../img/icons/arrow-right.svg") no-repeat 0 0 / 100% 100%;
  }

  @media screen and (min-width: $md-viewport) {
    margin: 0;
    padding: 1.4rem 2.5rem;
  }
}

.promo__decoration__card {
  &--orange {
    bottom: 58vw;
    right: 2rem;
    width: 40vw;
    border-radius: 0.75rem;
    rotate: 9.3deg;

    @media screen and (min-width: $md-viewport) {
      width: 18.9vw;
      right: 5vw;
      bottom: 46vw;
    }

    @media screen and (min-width: $lg-viewport) {
      width: 12.8rem;
      bottom: auto;
      top: 9rem;
      right: 7rem;
    }
  }

  &--white {
    left: 0.3rem;
    bottom: 25vw;
    rotate: -15.8deg;
    width: 41.5vw;
    border-radius: 1.25rem;

    @media screen and (min-width: $md-viewport) {
      width: 22vw;
      left: auto;
      right: 37.2vw;
    }

    @media screen and (min-width: $lg-viewport) {
      width: 15rem;
      bottom: 12rem;
      right: 31rem;
    }
  }
}

.promo__map {
  overflow: hidden;
  width: 100%;
  height: 37.5rem;
  background: #000;
  border-radius: 1.5rem;
}

.promo--about .promo__container::before {
  content: "";
  position: absolute;
  width: 115rem;
  height: 50rem;
  bottom: -26rem;
  background: url("../img/promo-about-desktop.png") no-repeat center center / 100% 100%;

}
