.acquiring {
  position: relative;
  margin: 0 0 3rem;
  padding: 5.25rem 0;
  border-radius: 1.5rem;
  background: radial-gradient(50% 50% at 50% 50%, #210202 0%, #000000 100%);
  color: $color-default-white;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: -20%;
    left: -54%;
    width: 300vw;
    height: 155vw;
    background: #F26B23;
    border-radius: 50%;
    opacity: 0.55;
    filter: blur(181.45px);
    rotate: -152.32deg;

    @media screen and (min-width: $md-viewport) {
      width: 120vw;
      height: 63vw;
      top: 40%;
      left: -100%;
      opacity: 1;
      z-index: auto;
      transform: rotate(-64.95deg);
    }

    @media screen and (min-width: $lg-viewport) {
      top: 67%;
      left: -104%;
    }
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    translate: 0 80%;
    bottom: 0;
    left: 100%;
    width: 160vw;
    height: 358vw;
    background: #EE4235;
    opacity: 0.4;
    filter: blur(181.45px);
    rotate: 158.35deg;

    @media screen and (min-width: $md-viewport) {
      width: 82vw;
      height: 124vw;
      z-index: auto;
      transform: rotate(156.16deg);
      opacity: 1;
      top: 20%;
      bottom: auto;
      translate: 20% 0;
    }

    @media screen and (min-width: $lg-viewport) {
      top: -14rem;
    }
  }

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 5.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 7.5rem;
    padding: 7.5rem 0 10.4rem;
  }
}

.acquiring__heading {
  margin: 0 0 0.75rem;
  text-align: center;
}

.acquiring__description {
  margin: 0 0 2.25rem;
  color: #e5e5e5;
  text-align: center;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 3rem;
    font-size: 1.13rem;
    line-height: 1.3;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 41.3rem;
    margin: 0 auto 9.3rem;
  }
}

.acquiring__wrapper {
  position: relative;
  z-index: 1;
}

.acquiring__decoration {
  margin: 0 auto 2.2rem;
  width: 22.5rem;
  aspect-ratio: 1 / 1;
  background: url("../img/acquiring-bg-mobile.svg") no-repeat 0 0 / 100% 100%;

  @media screen and (min-width: $lg-viewport) {
    width: 49.5rem;
    margin: 0 auto;
    background-image: url("../img/acquiring-bg-desktop.svg");
  }
}

.acquiring__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1.5rem;
}

.acquiring__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  gap: 1px 1.5rem;

  @media screen and (min-width: $md-viewport) {
    gap: 0.75rem 1.5rem;
  }

  @media screen and (min-width: $lg-viewport) {
    position: absolute;
    width: 41rem;

    &:nth-child(1) {
      top: -1rem;
      left: -8rem;
    }

    &:nth-child(2) {
      width: 24rem;
      top: 17rem;
      left: 0;
    }

    &:nth-child(3) {
      width: 25rem;
      bottom: 0;
      left: 8rem;
    }

    &:nth-child(4) {
      top: 2.6rem;
      right: 5rem;
      width: 29rem;
    }

    &:nth-child(5) {
      top: 23.6rem;
      right: -2rem;
      width: 25rem;
    }

    &:nth-child(6) {
      bottom: -0.6rem;
      right: 1rem;
      width: 28rem;
    }
  }

  &:nth-child(1), &:nth-child(2), &:nth-child(3) {
    text-align: right;
    grid-template-columns: 1fr min-content;

    .acquiring__item-icon {
      grid-column: 2 / 3;
    }

    .acquiring__item-text {
      margin-left: auto;
    }
  }
}

.acquiring__item-icon {
  position: relative;
  grid-row: 1 / 3;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  object-fit: cover;
  background: radial-gradient(135.33% 135.33% at 5.71% -44.13%, #000000 0%, rgba(0, 0, 0, 0.27) 100%);
  backdrop-filter: blur(37.1971px);

  img {
    width: 1.2rem;
    height: 1.2rem;

    @media screen and (min-width: $md-viewport) {
      width: 1.5rem;
      height: 1.5rem;
    }

    @media screen and (min-width: $lg-viewport) {
      width: 3rem;
      height: 3rem;
    }
  }

  @media screen and (min-width: $md-viewport) {
    width: 2.25rem;
    height: 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 4.5rem;
    height: 4.5rem;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 50%;
    padding: 1.5px;
    background: linear-gradient(104.59deg, rgba(255, 255, 255, 0.3) 0.76%, rgba(255, 255, 255, 0.0768416) 32.78%, rgba(255, 255, 255, 0.220526) 69.11%, rgba(255, 255, 255, 0.021) 99%);
    mask: linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
    mask-composite: exclude;
    pointer-events: none;
  }
}

.acquiring__item-name {
  margin: 0;
  color: #e5e5e5;
}

.acquiring__item-text {
  margin: 0;
  color: $color-gray;
  font-size: 0.88rem;
  line-height: 1.3;

  @media screen and (min-width: $md-viewport) {
    font-size: 1rem;
    width: 90%;
  }
}

