.risk-form {
  margin: 0 0 3rem;

  @media screen and (min-width: $md-viewport) {
    margin: 0 0 5.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 7.5rem;
  }
}

.risk-form__container {
  padding: 1.5rem;
  border-radius: 1.5rem;
  background: $gradient-primary;
  color: $color-default-white;

  @media screen and (min-width: $md-viewport) {
    padding: 3rem 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    padding: 3rem 9.3rem;
  }
}

.risk-form__heading {
  margin: 0 0 0.75rem;
  text-align: center;

  @media screen and (min-width: $md-viewport) {
    margin: 0 3.3rem 0.75rem;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 45rem;
    margin: 0 auto 0.75rem;
  }
}

.risk-form__description {
  margin: 0 0 2.25rem;
  color: #e5e5e5;
  text-align: center;

  @media screen and (min-width: $md-viewport) {
    margin: 0 3.3rem 2.25rem;
    font-size: 1rem;
    line-height: 1.3;
  }

  @media screen and (min-width: $lg-viewport) {
    width: 45rem;
    margin: 0 auto 2.25rem;
  }
}

.risk-form__wrapper {
  display: grid;
  gap: 1.5rem;

  @media screen and (min-width: $lg-viewport) {
    grid-template-columns: 1fr 1fr min-content;
  }
}

.risk-form__button {
  @media screen and (min-width: $lg-viewport) {
    padding: 0 2.5rem;
    height: 100%;
  }
}
