.licensed {
  position: relative;
  margin: 0 0 3rem;

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 5.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    margin: 0 0 7.5rem;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    translate: 0 10%;
    width: 641.31px;
    height: 1011px;
    background: #EE4235;
    opacity: 0.4;
    filter: blur(181.45px);
    transform: rotate(-95.86deg);
    z-index: -1;

    @media screen and (min-width: $md-viewport) {
      bottom: -43%;
    }
  }
}

.licensed__container {
  padding: 1.5rem;
  background: $color-default-white;
  border-radius: 1.5rem;

  @media screen and (min-width: $md-viewport) {
    padding: 3rem 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 1.25rem
  }
}

.licensed__heading {
  margin: 0 0 1.5rem;
  text-align: center;

  @media screen and (min-width: $lg-viewport) {
    grid-column: 1 / -1;

    &:nth-of-type(2) {
      margin: 0 0 3rem;
    }
  }
}

.licensed__text {
  margin: 0 0 1.5rem;
  color: rgba($color-default-black, 60%);
  text-align: center;

  @media screen and (min-width: $md-viewport) {
    font-size: 1.1rem;
  }

  @media screen and (min-width: $lg-viewport) {
    text-align: left;
    align-self: center;
  }
}

.licensed__block {
  margin: 0 0 2.25rem;
  padding: 1.5rem;
  border-radius: 1.5rem;
  background: $gradient-primary;
  color: $color-default-white;
  text-align: center;

  @media screen and (min-width: $md-viewport) {
    padding: 2.25rem;
  }

  @media screen and (min-width: $lg-viewport) {
    text-align: left;
  }
}

.licensed__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1.5rem;

  @media screen and (min-width: $lg-viewport) {
    grid-template-columns: 1fr 1fr;
    grid-column: 1 / -1;
  }
}

.licensed__item {
  display: grid;
  grid-template-columns: 1.88rem 1fr;
  gap: 0.75rem 1.5rem;
}

.licensed__item-name {
  margin: 0;
}

.licensed__item-icon {
  grid-row: 1 / 3;
}

.licensed__item-text {
  margin: 0;
  color: $color-gray;

  @media screen and (min-width: $md-viewport) {
    font-size: 1rem;
  }
}
