// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);
$color-accent-secondary:                   #162b3f;
$color-text-primary:                $color-default-black;
$color-text-secondary:              rgba($color-default-black, 60%);
$color-gray:                        #999999;
$gradient-primary: linear-gradient(90deg, #F26F21 0%, #EF522D 64.01%, #EE4036 100.02%);


// Typography
// ---------------------------------

$ff-inter:                        "Inter", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// Viewports
// ---------------------------------

$sm-viewport: 576px;
$md-viewport: 46.25rem;
$lg-viewport: 90rem;
